<template>
<div><separator></separator>
    <div class="page-content">
        <div>
          <el-input
              class="mini-input"
              placeholder="设备组"
              v-model="searchFrom.deviceGroupName"
              style="width:150px">
          </el-input>
          <el-button type="primary" class='search-button' icon="el-icon-search" @click="getData()" >查询</el-button>          
          <el-button type="primary" @click="createDialogVisible = true">创建新分组</el-button>
        </div>
        <div class="table-box" ref="tableBox">  
            <template>
                <el-table
                    ref="table"
                    :data="tableData"
                    style="width: 100%"
                    :default-sort = "{prop: 'id', order: 'ascending'}"
                    >
                    <el-table-column
                      fixed
                      type="selection"
                      width="55"
                    ></el-table-column>
                    <el-table-column
                    v-for="(tab,index) in tableDataLabel"
                    sortable
                    :key="index"
                    :prop="tab.property"
                    :label="tab.name"
                    :min-width="tab.width">
                    </el-table-column>

                            <el-table-column
                            fixed="right"
                            label="操作"
                            width="150">
                            <template slot-scope="scope">
                                <el-button
                                @click.native.prevent="view(scope.row)"
                                type="text"
                                size="small">
                                查看
                                </el-button>  
                                <el-button
                                @click.native.prevent="remove(scope.row)"
                                type="text"
                                class="del-btn"
                                size="small">
                                删除
                                </el-button>                      
                            </template>
                            </el-table-column>                    
                </el-table>
            </template>
        </div>
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10,20]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>


        <el-dialog
        title="新增"
        :visible.sync="createDialogVisible"
        width="60%"
        :before-close="createDialogFun">
            <el-form :model="createForm" status-icon :rules="createFormRules" ref="createForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="设备组名称" prop="name">
                <el-input  v-model="createForm.name" auto-complete="off"></el-input>
            </el-form-item>
            </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="()=>{createDialogVisible = false;nulltoForm(createForm,'createForm')}">取 消</el-button>
            <el-button type="primary" @click="submitForm('createForm')">确 定</el-button>
        </span>
        </el-dialog>

        <el-dialog
        title="查看设备组设备"
        :visible.sync="viewDialogVisible"
        width="60%"
        :before-close="viewDialogFun">
        </el-dialog>
    </div>
    </div>
</template>

<script>
import separator from "../../../../public/separator.vue";
import APICONFIG from "../../../../../config/api.js";
import TABLE_TYEPS from "../../../../../config/tableType.js";
export default {
  components: {
    separator
  },
  data() {
    return {
      viewDialogVisible: false, //修改弹框
      createDialogVisible: false, //添加弹框
      searchFrom: {
        deviceGroupName: ""
      },
      createFormRules: {},
      createForm: {},
      tableDataLabel: "",
      netAPI: "",
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      total: 0
    };
  },
  created() {
    this.tableDataLabel = TABLE_TYEPS.get("device_group");
    this.netAPI = APICONFIG().get("device_group");
    this.getData();
  },
  methods: {
    getData(page = { pageSize: this.pageSize, pageNo: this.currentPage }) {
      for (let i in this.searchFrom) {
        page["deviceGroupName"] = this.searchFrom[i];
      }
      this.$http.get(this.netAPI.find, { params: page }).then(res => {
        this.tableData = res.body.data;
        this.total = res.body.total;
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          switch (formName) {
            case "createForm":
              let fr = {
                deviceGroupName: this.createForm.name
              };
              this.$http.post(this.netAPI.insert, fr).then(res => {
                if (res.body.status == 200) {
                  this.getData();
                  this.$message({
                    message: "创建成功",
                    type: "success"
                  });
                  this.createDialogFun();
                }
              });

              break;
            case "editForm":
              this.editFormFun();
              break;
            default:
              break;
          }
        } else {
          this.$message.error("请填写必填信息");
          return false;
        }
      });
    },
    searchForm(formName) {},
    view(row) {
      //编辑框
      this.viewDialogVisible = true;
      for (let index in this.editForm) {
        this.viewForm[index] = row[index];
      }
    },
    viewDialogFun() {},
    remove(row) {
      let ids = [];
      if (!row) {
        this.$message({
          message: "请选中要删除的行",
          type: "warning"
        });
        return;
      }
      //判断类型
      let rowType = Object.prototype.toString.call(row);
      if (rowType == "[object Array]") {
        for (let index in row) {
          ids.push(row[index].id);
        }
      } else {
        ids.push({ id: row.id });
      }

      this.$confirm("确定要删除选中的行?", "删除提示", {
        distinguishCancelAndClose: true,
        confirmButtonText: "删除",
        cancelButtonText: "不删除"
      }).then(() => {
        //确认删除
        this.$http.post(this.netAPI.delete, ids).then(res => {
          if (res.data.status == 200) {
            this.getData();
            this.tableData = [];
          }
        });
      });
    },
    createDialogFun() {
      this.nulltoForm(this.createForm, "createForm");
      this.createDialogVisible = false;
    },
    editDialogFun() {
      this.nulltoForm(this.editForm, "editForm");
      this.editDialogVisible = false;
    },
    nulltoForm(obj, formName) {
      for (let i in obj) {
        obj[i] = "";
      }
      this.$refs[formName].resetFields();
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.getData({ pageSize: this.pageSize, pageNo: this.currentPage });
    },
    handleCurrentChange(Current) {
      this.currentPage = Current;
      this.getData({ pageSize: this.pageSize, pageNo: this.currentPage });
    }
  }
};
</script>
<style lang="scss" scoped>
.sub {
  text-align: right;
}
.page-content {
  background: #fff;
  padding: 13px;
}
</style>



